import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";
import "./App.css";
import img from "./glogin.png";
import { connect } from "react-redux";
import * as actions from "../actions";
import Spinner from "react-spinner";
import StripeCheckout from "react-stripe-checkout";
import axios from "axios";
import ServeyForm from "./servey/ServeyForm";
import ServeyNew from "./servey/ServeyNew";
import Login from "./Login";
import Forgot from "./Forgot";
import Reset from "./Reset";
window.axios = axios;

const logout = () => <h1>Loggedout only..</h1>;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { amount: 500 };
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    this.props.fetchUser();
  }

  handleChange(event) {
    this.setState({ amount: Number(event.target.value) });
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Welcome to BULK EMAIL</h1>
        </header>
        <p className="App-intro">
          Emaily is a Bulk Mail <code> {"< Code App />"} </code>
          FRONT:(built with react/redux/stripe) BACK:
          (node/express/mongo/stripe/node-mailer)
        </p>

        <span className="stripe">
          <h1 style={{ opacity: "0.3" }}>
            {" "}
            <code> {"< BuLk EmaIl />"} </code>
          </h1>
          <div className="stripe-amount" >
            <form style={{display:"flex"}}>
              <select onChange={this.handleChange} style={{display:"flex", opacity:0.5}}>
                <option value="500"> $5 </option>
                <option value="1000"> $10 </option>
                <option value="1500"> $15 </option>
              </select>
            </form>

            <StripeCheckout
              amount={this.state.amount}
              token={(token, amount) =>
                this.props.stripe(token.id, this.state.amount)
              }
              stripeKey="pk_test_qcwFfT349quA1FJkGTIVqLYX"
            > <button className="logbut"> Pay with stripe  </button>
    </StripeCheckout>

          </div>

          {this.logIn()}
        </span>

        <BrowserRouter>
          <div>
            <Route exact path="/forgot" component={Forgot} />
            <Route exact path="/reset/:token" component={Reset} />
            <Route path="/api/logout" component={logout} />
            {this.props.user ? (
              <div>
                <Route path="/serveys/new" component={ServeyNew} />
                <Route exact path="/" component={ServeyForm} />
              </div>
            ) : (
              <Route exact path="/" component={Login} user={this.props.user} />
            )}
          </div>
        </BrowserRouter>
      </div>
    );
  }

  logIn = () => {
    if (this.props.user === null) {
      return <Spinner />;
    }

    if (this.props.user) {
      console.log(this.props.user.googleId);
      console.log(`user credits`, this.props.user.credits);

      return (
        <span className="stripe-amount">
          Credit:{" "}
          {this.props.user.credits === null && this.props.user.credits <= 0 ? (
            <Spinner />
          ) : (
            this.props.user.credits
          )}
          <button onClick={() => this.props.logout()} style={{opacity:0.7}}>log out</button>
        </span>
      );
    } else
      return (
        <span className="stripe-amount">
          <a href="/auth/google">
            {" "}
            <img src={img} height="50px" width="200px" alt="google"/>
          </a>
        </span>
      );
  };
}

function mapStateToProps(state) {
  return { user: state.user };
}

export default connect(mapStateToProps, actions)(App);



